import React, {useState, useEffect} from 'react';
import styled from "styled-components";
import {Grid } from '@geist-ui/react';
import ListItem from "./components/listItem";
import Detail from "./components/detail";
import StakeModal from "./components/modal";
import Loading from "./components/loading";
import Inviting from "./components/invite";

import services from "./api/services";
import {useWeb3} from "./api/contracts";

const Row = styled.div`
  margin: 117px 0 92px;
  padding: 0;
    @media (max-width: 768px) {
    margin-bottom: 24px;
  }
`;

export default function Home(props) {
    const {state,dispatch} = useWeb3();
    const { maincontract,web3Instance,Account, loading,invite} = state;

    const [showDetail, setShowDetail] = useState(false);
    const [showModal, setShowModal] = useState(false);
    const [list, setList] = useState([]);
    const [detailId, setDetailId] = useState(0);
    const [name, setName] = useState('');


    useEffect(() => {
        if(maincontract==null || web3Instance == null || showModal) return;
        const getServicesList = async () =>{
            dispatch({type: 'SET_LOADING',payload:{
                tips:"Get Services List...",
                }});
            services.getServicesList(maincontract, Account).then((data)=>{
                dispatch({type: 'SET_LOADING',payload:null});
                if(data.length){
                   let dataAfter =  data.slice().sort((a, b)=>  parseInt(a.created) > parseInt(b.created) ? -1 : 1);
                    setList(dataAfter)
                }
            })
        };
        getServicesList()


    //     if(Account == null ) return;
    //     // Test code
    //     //<<<
    //     const registerService = async () => {
    //         services.regService(maincontract, Account).then((data) => {
    //             console.error("=====registerService", data);
    //         })
    //     }
    //     registerService();
    // // >>>





    }, [maincontract,web3Instance,showModal,Account]);


    useEffect(()=>{
        const init = async()=>{
            const tokenAddress = '0xbaDd22fA317b453f9343eA8A3971a0E0102899a2';
            const tokenSymbol = 'ZHT';
            const tokenDecimals = 18;
            const tokenImage = 'http://zerohybrid.network/img/favicon.ico';

            let addToken = JSON.parse(localStorage.getItem("tokenAdd"));
            if(!addToken){
                try {
                    // wasAdded is a boolean. Like any RPC method, an error may be thrown.
                    const wasAdded = await window.ethereum.request({
                        method: 'wallet_watchAsset',
                        params: {
                            type: 'ERC20', // Initially only supports ERC20, but eventually more!
                            options: {
                                address: tokenAddress, // The address that the token is at.
                                symbol: tokenSymbol, // A ticker symbol or shorthand, up to 5 chars.
                                decimals: tokenDecimals, // The number of decimals in the token
                                image: tokenImage, // A string url of the token logo
                            },
                        },
                    });

                    if (wasAdded) {
                        localStorage.setItem("tokenAdd", true);
                        console.log('Thanks for your interest!');
                    } else {
                        localStorage.setItem("tokenAdd", false);
                        console.log('Your loss!');
                    }
                } catch (error) {
                    console.log(error);
                }
            }
        }
        init()
    },[])

    const closeAll = () =>{
        setShowDetail(false);
        setShowModal(false);
    }
    const openBox = (id) =>{
        setDetailId(id);
        setShowDetail(true);

    }
    const openModal = (id,str) =>{
        setShowDetail(false);
        setShowModal(true);
        setDetailId(id);
        setName(str);

    }
    return (
        <div className="maincontain">
            {
                showDetail && <Detail closeBox={closeAll} detailId={detailId} openModal={openModal}/>
            }
            {
                showModal && <StakeModal closeBox={closeAll} detailId={detailId} name={name}/>
            }
            {
                loading!= null && <Loading tips={loading.tips} />
            }
            {
                invite ==='show' && <Inviting />
            }

            <Row>
                <Grid.Container gap={4}>
                    {
                        list && list.map((item)=>(
                            <Grid xs={24} md={12} key={`serviceList_${item.id}`}>
                                <ListItem openBox={openBox} openModal={openModal} Itemdata={item}/>
                            </Grid>
                        ))
                    }
                </Grid.Container>
            </Row>

        </div>
    );
}
