export default function MyItem(props) {
    const {state, dispatch} = useWeb3();
    const {maincontract, web3Instance, Account} = state;
    const {
        handleRefresh, Itemdata: {
            spname, logo, name,
            myStake,
            status,
            myTotal,
            myAvailable,
            myClaimed,
            period,
            // threshold, created,  grace,
            id
        }
    } = props;


    const toThousands = (str) => {
        let balance = web3Instance.web3Instance.utils.fromWei(str);
        return PublicObj.ToThousands(balance);
    }

    const handleclaim = () => {

        dispatch({type: 'SET_LOADING', payload: {tips: "Claim..."}});
        services.claim(maincontract, id, Account).then(() => {
            dispatch({type: 'SET_LOADING', payload: null});
            dispatch({type: 'BALANCE_INIT'});
            handleRefresh();
        }).catch(() => {
            dispatch({type: 'SET_LOADING', payload: null});
            dispatch({type: 'BALANCE_INIT'});
            handleRefresh();
        });
    }
    const handleRedeem = () => {
        dispatch({
            type: 'SET_LOADING', payload: {
                tips: "Redeem..."
            }
        });
        services.redeem(maincontract, id, Account).then(() => {
            dispatch({type: 'SET_LOADING', payload: null});
            dispatch({type: 'BALANCE_INIT'});
            handleRefresh();
        }).catch(() => {
            dispatch({type: 'SET_LOADING', payload: null});
            dispatch({type: 'BALANCE_INIT'});
            handleRefresh();
        });
    }

    // const switchTime = (dateTime) => {
    //     const daytime = parseInt(dateTime) * 1000;
    //     return new Date(daytime).toLocaleString("en");
    // }

    // const switchEndTime = (dateTime, grace) => {
    //     const times = parseInt(dateTime) + parseInt(grace);
    //     return switchTime(times);
    // }


    return (
        <Librdr>


            <Lirht>
                <div>

                    <ul>
                        {/*<Li>*/}
                        {/*    <TitleName> <Star>*</Star><Names>Finish Time</Names></TitleName>*/}
                        {/*    <div>*/}
                        {/*        <NumS>{switchEndTime(created, parseInt(grace) + parseInt(period))}</NumS>*/}
                        {/*    </div>*/}
                        {/*</Li>*/}
                        <Li>
                            <TitleName> <Star><img src={star} alt=""/></Star><RewardName>Staked</RewardName></TitleName>
                            <MyStake>
                                <NumS>{toThousands(myStake)} ZHT</NumS>
                            </MyStake>
                        </Li>
                        <Li>
                            {/*<TitleName> <Star>*</Star><Names>My Rewards</Names></TitleName>*/}
                            <TitleName> <Star><img src={star} alt=""/></Star></TitleName>
                            <Li24><RewardName>Total Rewards</RewardName><NumS>{toThousands(myTotal)} ZHT</NumS></Li24>
                            <Li24><RewardName>Available</RewardName><NumS>{toThousands(myAvailable)} ZHT</NumS></Li24>
                            <Li24><RewardName>Claimed</RewardName><NumS>{toThousands(myClaimed)} ZHT</NumS></Li24>
                        </Li>
                    </ul>
                </div>

            </Lirht>
            <Lilft>
                <div>
                    <FirstRow>
                        <LftT>
                            <Avatar src={logo} width="28px"/>
                            <Author>by {spname}</Author>
                        </LftT>

                    </FirstRow>
                    <Titles> {name}</Titles>
                </div>


                <LastRht>
                    <span>Period: {PublicObj.SecondToDay(period)} Days</span>

                    <div>
                        {/*0 created, 1 in progress, 2 finished, 3 failed.*/}
                        {
                            status === '0' && <Btn disabled>Claim</Btn>
                        }
                        {
                            status === '1' &&
                            <Btn onClick={() => handleclaim(id)}>Claim {toThousands(myAvailable)} ZHT</Btn>
                        }
                        {
                            (status === '2' || status === '3') && <BtnRe onClick={() => handleRedeem(id)}>Redeem</BtnRe>
                        }

                    </div>

                </LastRht>
            </Lilft>
        </Librdr>
    );

}
